<template>
<div class="el-dialog__wrapper">
	<div class="el-dialog el-dialog--100 cs_motai">
		<div class="el-dialog__header">
			<span class="el-dialog__title">{{details.realname}}</span>
			<div class="el-dialog__headerbtn" @click="hidden_contact">
				<i class="el-dialog__close el-icon el-icon-close"></i>
			</div>
		</div>
		<div class="el-dialog__body">
			<div class="cs_contact">
				<div class="imgdiv"><img :src="details.img" alt=""></div>
				<el-row :gutter="20">
				  	<el-col class="_name" :span="8"><img src="../../img/icon/xmicon.png" height="14" width="14"><span>{{details.realname}}</span></el-col>
				 	<el-col class="_name" :span="8"><img src="../../img//icon/lxicon.png" height="14" width="14"><span>{{details.jobsName}}</span></el-col>
				  	<el-col class="_name" :span="8"><img src="../../img/icon/dhicon.png" height="14" width="14"><span>{{details.cellphone}}</span></el-col>
				</el-row>
				<button v-on:click="f_vedioo" v-if="false">视频通话</button>
				<div class="cs_vedioo" v-if="is_vedioo"><vediocomponent></vediocomponent></div>
			</div>
		</div>
	</div>


</div>


</template>
<script>
import vedioo from "./vedio.vue";
export default{
  data:function(){
    return {
    	is_vedioo:false,
    	details:{},
    	dialogVisible:false,
    }
  },
  created:function(){
  	var that=this;
  	    const Bus = global.eventHub;
	  	Bus.$on('detail_contact', function(msg){
	  		// console.dir(msg);
	  		that.details=msg;
	  		that.dialogVisible=true;
	  	});
  },	 
   components: {
        vediocomponent:vedioo
    },
  methods:{
  	hidden_contact:function() {
        this.$emit('hidden_contact');
    },
    f_vedioo:function(){
    	//var that=this;
    	//this.is_vedioo=true;
    	//setTimeout(function(){
    	//	that.is_vedioo=false;
    	//},2000)
    }
  }
}

</script>





<style>
.cs_contact{
	/* position: relative; */
	width: 350px;
	height: 310px;
	background: #fff;

}
.cs_contact ._name{
	text-align: center;
	height: 30px;
	line-height: 30px;
	overflow: hidden;

}
.cs_contact ._name span{

	font-size: 9px;
	padding-left: 8px;

}
.cs_contact img{
	display: inline-block;
}
.imgdiv{
    height: 166px;
    margin: 15px 0 30px;

}
.cs_contact .imgdiv img{
	display: block;
	width: 135px;
	height: 166px;
	margin: 0 auto;
	border: none;
}
._info{
	height: 40px;
	text-align: center;
	line-height: 40px;
	font-size: 13px;
}
.cs_contact button{
	background: #999999;
	width: 98px;
	height: 30px;
	outline: none;
	border-radius: 8px;
	border: none;
	color: #fff;
	font-size: 12px;
	line-height: 30px;
	display: block;
	margin: 0 auto;
}
.cs_vedioo{
	position: absolute;
	top: 50%;
	left: -100%;
	transform: translate(-10px,-50%);
}
.cs_motai{
	right: 10px;
	left: auto;
	top: 120px;
	transform: translateX(0);
}
</style>



